<template>
  <div>
    <div class="alert alert-danger text-center" v-if="error">
      {{ error }}
    </div>

    <div class="row justify-content-center">
      <div class="form-group">
        <label for="year">Година</label>
        <b-form-select id="year" :options="years.options" @input="onSelect"></b-form-select>
      </div>
    </div>
    <div v-if="transactions && transactions.length !== 0">
        <div class="row my-5 justify-content-center">
          <table class="table table-striped" v-if="ready">
            <thead>
              <tr>
                <th scope="col" style="width:130px">Дата</th>
                <th scope="col">Номер</th>
                <th scope="col">Вид транзакция</th>
                <th scope="col">Продадена валута</th>
                <th scope="col">Количество продадена<br>валута</th>
                <th scope="col">Продажна цена (лв.)</th>
                <th scope="col">цена на придобиване (лв.)</th>
                <th scope="col">Резултат от сделката (лв.)</th>
              </tr>
            </thead>
              <tbody>
              <tr v-for="tx in transactions">
                <td>{{ tx.date }}</td>
                <td>{{ tx.transactionId }}</td>
                <td>{{ tx.type }}</td>

                <td>{{ tx.soldCcy }}</td>
                <td class="text-right">{{ parseFloat(tx.soldCcyAmount).toFixed(8) }}</td>

                <td class="text-right">{{ parseFloat(tx.salesProceedsBGN).toFixed(2) }}</td>
                <td class="text-right"><span v-if="tx.costBasisBGN">{{ parseFloat(tx.costBasisBGN).toFixed(2) }}</span></td>
                <td class="text-right"><span v-if="tx.profitLoss">{{ parseFloat(tx.profitLoss).toFixed(2) }}</span><span v-else>0.00</span></td>
              </tr>
            </tbody>
              <tfoot>
              <tr>
                  <td class="text-right" colspan="7"><b>Нетен резултат:</b></td>
                  <td class="text-right"><b>{{ parseFloat(endresult).toFixed(2)}}</b></td>
              </tr>

              </tfoot>
          </table>
        </div>
        <div v-if="days" class="row my-5 justify-content-center">
            <table class="table table-striped">
                <thead>
                <tr>
                    <th scope="col">Дата</th>
                    <th scope="col">Продажна цена</th>
                    <th scope="col">Цена на придобиване</th>
                    <th scope="col">Обща печалба</th>
                    <th scope="col">Обща загуба</th>
                </tr>
                </thead>
                <tbody>
                    <tr v-for="(value, name) in days">
                        <td>{{ name }}</td>
                        <td class="text-right"><span v-if="value.proceeds">{{ parseFloat(value.proceeds).toFixed(2) }}</span><span v-else>0.00</span></td>
                        <td class="text-right"><span v-if="value.price">{{ parseFloat(value.price).toFixed(2) }}</span><span v-else>0.00</span></td>
                        <td class="text-right"><span v-if="value.profitLoss >= 0">{{ parseFloat(value.profitLoss).toFixed(2) }}</span><span v-else>0.00</span></td>
                        <td class="text-right"><span v-if="value.profitLoss <= 0">{{ parseFloat(value.profitLoss).toFixed(2) }}</span><span v-else>0.00</span></td>
                    </tr>
                </tbody>
            </table>
        </div>
        <div v-if="endsums" class="row my-5 justify-content-center">
            <table class="table table-striped text-right" v-if="ready">
                <thead>
                <tr>
                    <th class="text-left" scope="col">Валута</th>
                    <th scope="col">Количество</th>
                    <th scope="col">Средна цена на придобиване</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(value, name) in endsums">
                    <td class="text-left">{{ name }}</td>
                    <td><span v-if="value.quantity">{{ customFixed(value.quantity)}}</span></td>
                    <td><span v-if="value.price">{{ parseFloat(value.price).toFixed(2) }}</span><span v-else>0.00</span></td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
      <div v-else>
          <div v-if="ready">Няма продажби или замяна на криптовалути през избраната данъчна година
          </div>
      </div>
      <!--    <div v-if="!total">There are no any transactions.</div>-->

    <loading :loading="loading"></loading>

  </div>
</template>

<script>
  import axios from 'axios'
  import Decimal from 'decimal.js'

  Decimal.set({ toExpNeg: -14 })
  export default {
    created() {
      this.loadTransaction(this.portfolioId)
    },
    props: ['portfolioId'],
    data() {
      return {
        loading: false,
        error: null,
        years: {
          // selected: 1,
          options: [],
        },
        pId: null,
        ready: false,
        transactions: null,
        days: null,
        currencies: null,
        endsums: null,
        endresult: null,
        total: null,
      }
    },
    methods: {
      loadTransaction(portfolioId) {
        this.pId = portfolioId
        let yearsId = 0
        axios.get(`${process.env.API_V1_URL}/portfolio/${portfolioId}/tx/years`)
          .then(res => {
            this.years.options = [{value: '', text: ''}].concat(res.data.map((date, i) => {
              return {value: ++yearsId, text: date}
            }))
          })
      },
      customFixed(str, num = 8) {
        const rgx = new RegExp(`-?\\d+\\.?\\d{0,${num}}`)
        str = (`${str}`).match(rgx)
        return str ? parseFloat(str[0]).toFixed(num) : parseFloat(0).toFixed(num)
      },
      onSelect(v) {
        this.ready = false
        this.error = null
        this.loading = true

        if (this.years.options[v]) {
          return axios.get(`${process.env.API_V1_URL}/portfolio/${this.pId}/tx${this.years.options[v].text ? '/year/' + this.years.options[v].text : ''}`)
            .then(res => {
              this.transactions = res.data.transactions
              this.days = res.data.days
              this.currencies = res.data.currencies
              this.endsums = res.data.endsums
              this.endresult = res.data.endresult
              this.total = res.count
            })
            .catch(errMsg => {
              this.error = errMsg
            })
            .finally(() => {
              this.loading = false
              this.ready = true
            })
        } else {
          this.ready = false
          this.error = null
          this.loading = false
          return true
        }
      },
    },
    watch: {
      portfolioId(newId) {
        if (newId) {
          this.loadTransaction(newId)
        }
      },
    },
  }
</script>
<style scoped>
    .table thead th {
        vertical-align: text-top;
        /*text-align: right;*/
    }
</style>



